import { Location } from '@angular/common';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Action, Selector, State, StateContext, Store } from '@ngxs/store';
import { ApiStatus, updateApiStatus } from 'src/app/interfaces/api-status';
import {
  StudentAdmission,
  StudentAdmissionRaw,
} from 'src/app/models/student-admission';
import { environment } from 'src/environments/environment';
import { ClearAllStates } from '../actions/global.actions';
import {
  GetStudentAdmissions,
  HandleAdmissionsResponse,
  SetSelectedGuid,
  SetSelectedNodeId,
} from '../actions/student-admissions.actions';
import { normalizeAdmission } from './normalizers/student-admission.normalizer';
import { ParticipantState } from './participant.state';

export interface StudentAdmissionsStateModel extends ApiStatus {
  selectedGuid: string;
  selectedNodeId?: string;
  selectedAdmissionStatus?: string;
  selectedAdmissionContextColor: string;
  selectedAdmissionLocked: boolean;
  studentAdmissionsRaw: StudentAdmissionRaw[];
}

const defaultState: StudentAdmissionsStateModel = {
  hasApiError: false,
  hasConnectionError: false,
  isFetching: false,
  isInitialised: false,
  selectedGuid: '',
  selectedNodeId: '',
  selectedAdmissionLocked: false,
  selectedAdmissionContextColor: null,
  studentAdmissionsRaw: [],
};

@State<StudentAdmissionsStateModel>({
  name: 'studentAdmissions',
  defaults: { ...defaultState },
})
@Injectable()
export class StudentAdmissionsState {
  private headers: HttpHeaders = new HttpHeaders({
    apiKey: environment.apiKey
  })
  constructor(
    private store: Store,
    private http: HttpClient,
    private router: Router,
    private location: Location
  ) {}

  @Selector()
  static apiStatus({
    hasApiError,
    hasConnectionError,
    isFetching,
    isInitialised,
  }: StudentAdmissionsStateModel): ApiStatus {
    return {
      hasApiError,
      hasConnectionError,
      isFetching,
      isInitialised,
    };
  }

  @Selector()
  static selectedGuid({ selectedGuid }: StudentAdmissionsStateModel): string {
    return selectedGuid;
  }

  @Selector()
  static selectedNodeId({ selectedNodeId }: StudentAdmissionsStateModel): string {
    return selectedNodeId;
  }

  @Selector()
  static selectedAdmissionStatus({ selectedAdmissionStatus }: StudentAdmissionsStateModel): string {
    return selectedAdmissionStatus;
  }

  @Selector()
  static selectedAdmissionContextColor({ selectedAdmissionContextColor }: StudentAdmissionsStateModel): string {
    return selectedAdmissionContextColor;
  }
  @Selector()
  static selectedAdmissionLocked({ selectedAdmissionLocked }: StudentAdmissionsStateModel): boolean {
    return selectedAdmissionLocked;
  }

  @Selector()
  static studentAdmissions({
    studentAdmissionsRaw,
  }: StudentAdmissionsStateModel): StudentAdmission[] {
    const normalized = studentAdmissionsRaw.map((s) => normalizeAdmission(s));
    return normalized;
  }

  @Action(SetSelectedNodeId)
  async setSelectedNodeId(
    ctx: StateContext<StudentAdmissionsStateModel>,
    { id }
  ): Promise<void> {
    ctx.patchState({selectedNodeId:id});
  }
  @Action(GetStudentAdmissions)
  async getStudentAdmissions(
    ctx: StateContext<StudentAdmissionsStateModel>,
    { guid },
    // participantId?: string
  ): Promise<void> {
    ctx.patchState({
      ...updateApiStatus({ isFetching: true }),
    });
    this.http.get<StudentAdmissionRaw[]>(this.constructUrl(),{headers:this.headers}).subscribe({
      next: (data) => ctx.dispatch(new HandleAdmissionsResponse(data, guid)),
      error: (e: any) => {
        console.warn(e.message || e);
        this.router.navigate(['/', 'logout']);
      },
    });
  }

  @Action(HandleAdmissionsResponse)
  handleAdmissionsResponse(
    ctx: StateContext<StudentAdmissionsStateModel>,
    { data, guid }: HandleAdmissionsResponse
  ): void {
    const studentAdmission = guid?data.filter(admission => admission.Guid === guid)[0]:data[0];
    ctx.setState(() => ({
      ...updateApiStatus({ isInitialised: true }),
      studentAdmissionsRaw: data,
      selectedGuid: studentAdmission.Guid,
      selectedAdmissionStatus: studentAdmission.AdmissionStatusText,
      selectedAdmissionLocked: studentAdmission.AdmissionLock,
      selectedAdmissionContextColor: ''+studentAdmission.ContextualColor
    }));
    this.location.go('home/'+studentAdmission.Guid);
  }

  @Action(SetSelectedGuid)
  setSelectedGuid(
    ctx: StateContext<StudentAdmissionsStateModel>,
    action: SetSelectedGuid
  ): void {
    ctx.setState((oldState) => ({ ...oldState, selectedGuid: action.guid, selectedAdmissionStatus: action.status, selectedAdmissionLocked: action.locked, selectedAdmissionContextColor: action.contextColor }));
    this.location.go('home/'+action.guid);
  }

  @Action(ClearAllStates)
  clearState(ctx: StateContext<StudentAdmissionsStateModel>): void {
    ctx.setState({ ...defaultState });
  }

  constructUrl(): string {
    const id = this.store.selectSnapshot(ParticipantState.participantId);
    return `${environment.endpoints.root+environment.endpoints.student}/${id}/${environment.endpoints.admissions}`;
  }
}
