import { Injectable } from '@angular/core';
import { State, Selector, Action, StateContext } from '@ngxs/store';
import { ClearDataUploadState, SetDataUploadState } from '../actions/data-upload.actions';

export interface DataUploadStateModel {
  formData: FormData;
  completed: boolean;
  errors: string[];
}
export const defaultState: DataUploadStateModel = {
  formData: null,
  completed: null,
  errors: [],
};
@State<DataUploadStateModel>({
  name: 'dataUpload',
  defaults: { ...defaultState },
})
@Injectable({
  providedIn: 'root',
})
export class DataUploadState {
  @Selector()
  static dataUploadState(stream: DataUploadStateModel): {} {
    return stream;
  }

  @Action(SetDataUploadState)
  setDataUploadState(
    ctx: StateContext<DataUploadStateModel>,
    payload: DataUploadStateModel
  ): void {
    ctx.setState(
      (oldState): DataUploadStateModel => ({
        ...oldState,
        ...payload
      })
    );
  }
  @Action(ClearDataUploadState)
  clearDataUploadState(ctx: StateContext<DataUploadStateModel>): void {
    ctx.setState(
      (oldState): DataUploadStateModel => ({
        ...oldState,
        completed: null,
        formData: null,
        errors: [],
      })
    );
  }
}
