import { Component, NgZone, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { App } from '@capacitor/app';
import {
  faBars,
  faHome,
  IconDefinition,
  faEnvelope
} from '@fortawesome/free-solid-svg-icons';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { SafariViewController } from '@ionic-native/safari-view-controller/ngx';
import { ActionSheetController, NavController } from '@ionic/angular';
import { ActionSheetButton } from '@ionic/core';
import { TranslateService } from '@ngx-translate/core';
import { Select, Store } from '@ngxs/store';
import { Observable, Subject } from 'rxjs';
import { filter, takeUntil, tap } from 'rxjs/operators';
import { MenuItemOptions, MenuObject } from './interfaces/content';
import { LinkService } from './services/link.service';

import { PlatformService } from './services/platform.service';
import { StorageService } from './services/storage.service';
import { ToggleHelpIsActive } from './store/actions/content.actions';
import { MenuState } from './store/state/menu.state';
import { ParticipantState } from './store/state/participant.state';
import { MuMenuPopoverComponent } from './ui-components/elements/mu-menu-popover/mu-menu-popover.component';
@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent
implements OnInit, OnDestroy{
  shouldRenderNativeNavigation: boolean;
  faHome: IconDefinition = faHome;
  faBars: IconDefinition = faBars;
  faEnvelope: IconDefinition = faEnvelope;
  notificationCount: number;
  participantId: string;
  ngUnsubscribe = new Subject();
  public menuItems: ActionSheetButton[];


  @Select(ParticipantState.participantId)
  participantIdStream: Observable<string>;

  constructor(
    private actionsheetCtrl: ActionSheetController,
    private navCtrl: NavController,
    private platform: PlatformService,
    private storageService: StorageService,
    private translateService: TranslateService,
    private router: Router,
    private zone: NgZone,
    private store: Store,
    private linkService: LinkService
  ) {}

  ngOnInit() {
    this.storageService.initializeStoreData();
    this.onOpenFromBrowser();
    this.listenToParticipantChanges();
    this.listenToSelectedMenuChanges();
    this.shouldRenderNativeNavigation = !this.platform.isWeb();
  }

  onOpenFromBrowser() {
    let path: string;
    let query: string;
    let code: string;
    let rest: string;
    App.addListener('appUrlOpen', ({ url }: { url: string }) => {
      this.zone.run(() => {
        const [urlScheme, route] = url.split('://');
        if (route) {
          [path, rest] = route.split('?');
          [rest, query] = route.split('code=');
          [code] = query && query.split('&');
        }
        if (path && code) {
          this.router.navigate(['/', path], { queryParams: { code } });
        }
      });
    });
  }

  goToHome() {
    this.navCtrl.navigateBack(['/', 'home']);
  }

  goToMessages(){
    this.router.navigate(['/','messages']);
  }

  @Select(MenuState.selectedMenu)
  selectedMenuStream: Observable<MenuObject[]>;

  private listenToSelectedMenuChanges(): void{

    this.selectedMenuStream
      .pipe(
        takeUntil(this.ngUnsubscribe),
        filter((value) => !!value),
        tap((menuObjects: MenuObject[]) => {
          this.menuItems = menuObjects.map((item: MenuObject) => {
            return {
              text: item.title,
              role: 'destructive',
              handler: () => {
                this.linkService.openUrl(item.url, item.options);
              }
            };
          });
        })
      )
      .subscribe();
  }

  async presentNativeMenu() {
    const actionSheet = await this.actionsheetCtrl.create(
    {
      header: 'Menu',
      cssClass: 'native-menu',
      buttons: [
        ...this.menuItems,
        {
          text: this.translateService.instant('elements.inbox'),
          role: 'destructive',
          icon: 'mail',
          handler: () => {
            this.router.navigate(['/', 'messages']);
          }
        },
        {
          text: this.translateService.instant('elements.help'),
          role: 'destructive',
          icon: 'help-circle',
          handler: async () => {
            await actionSheet.dismiss();
            this.store.dispatch(new ToggleHelpIsActive());
          }
        },
        {
          text: this.translateService.instant('elements.logout'),
          role: 'destructive',
          icon: 'log-out',
          handler: () => {
            this.router.navigate(['/', 'logout']);
          },
        },
      ],
    });
    await actionSheet.present();
  }

  private listenToParticipantChanges(): void{
    this.participantIdStream.pipe(
      takeUntil(this.ngUnsubscribe),
      tap((participantId) => {
        this.participantId = participantId;
      })
    )
    .subscribe();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.unsubscribe();
  }


}
